        <el-step title="修改成功" icon="el-icon-check"></el-step>
      </el-steps>
      <div class="h-item">
        <span class="title">验证方式</span>
        <el-input v-model="account" style="width:500px;">
          <el-select v-model="codeFrom" @change="onCodeFromChange" slot="prepend" style="width:120px;">
            <el-option label="手机" value="sms"></el-option>
            <el-option label="邮箱" value="email"></el-option>
          </el-select>
